import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import { Container } from '@mui/material';
import { Basket } from '../Basket/Basket';
import SetMealIcon from '@mui/icons-material/SetMeal';

export const Header = () => {

   return (
      <AppBar
         position='static'
         component='header'
         sx={{ background: '#041d3e' }}>
         <Container maxWidth="lg">
            <Toolbar>
               <Typography variant="h5" href='##' component="a" sx={{ flexGrow: 1, display: 'flex', alignItems: 'center', gap: '10px', textDecoration: 'none', color: '#e6b23d' }}>
                  <SetMealIcon sx={{ fontSize: '50px' }} /> React Sushi
               </Typography>
               < Basket />
            </Toolbar>
         </Container>
      </AppBar>
   );
}