import { Box, Container, Typography } from "@mui/material"
import Grid from '@mui/material/Unstable_Grid2';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import { useContext, useEffect, useState } from "react";
import { GetSushiList } from "../../services/sushiAPI";
import { context } from "../../context/context";

const sort = [' ', ' ']


export const Main = () => {
   const ctx = useContext(context)
   const [activeSort, setActiveSort] = useState(null);

   useEffect(() => {
      if (ctx.sushiList.length !== 0) {
         return
      }
      async function getSushiList() {
         try {
            const response = await GetSushiList()
            ctx.getSushi(response)
         } catch (error) {
            console.log(error.message);
         }
      }

      getSushiList()
   }, [ctx])

   useEffect(() => {
      if (!ctx.sushiInBasket) {
         return
      }
      window.localStorage.setItem('sushiInBasket', JSON.stringify(ctx.sushiInBasket));

   }, [ctx.sushiInBasket]);

   const handleInBasket = position => {
      ctx.addSushi(position)
   }

   const handleSortBy = (index) => {
      setActiveSort(index)
      if (index === 0) {
         ctx.sortByPrice()
      }
      if (index === 1) {
         ctx.sortByWeight()
      }
   }

   return (
      <Box component='main'>
         <Box component='section' py='100px'>
            <Container maxWidth='lg'>
               <Typography
                  variant="h3"
                  component='h1'
                  mb='50px'
                  sx={{ textAlign: 'center' }}>
                   
               </Typography>
               <Box sx={{ display: 'flex', gap: '20px', mb: '20px' }}>
                  {sort.map((item, index) => (
                     <Button sx={activeSort === index ? { background: '#e6b23d' } : { background: '#dbf3fd' }} onClick={() => handleSortBy(index)} key={item}>{item}</Button>
                  ))}
               </Box>
               <Grid container spacing={5}>
                  {ctx.sushiList.map(({ id, img, name, text, weight, price }) => (
                     <Grid key={id} xs={12} sm={6} md={4} sx={{ display: 'flex' }}>
                        <Card sx={{ display: 'flex', flexDirection: 'column', flex: '1 1 auto', p: '20px', background: '#f6fcff' }}>
                           <CardMedia
                              sx={{ height: '140px' }}
                              image={img}
                              title={name}
                           />
                           <CardContent sx={{ flex: '1 1 auto' }}>
                              <Typography gutterBottom variant="h5" component="div">
                                 {name}
                              </Typography>
                              <Typography variant="body2">
                                 {text}
                              </Typography>
                           </CardContent>
                           <CardActions sx={{ gap: '20px', display: 'flex', alignItems: 'center', justifyContent: 'space-around', flexWrap: 'wrap' }}>
                              <Typography sx={{ background: '#89fc89', p: '10px', borderRadius: '100%' }}>
                                 {weight}.
                              </Typography>
                              <Typography sx={{ fontWeight: '700' }}>
                                 {price} .
                              </Typography>
                              <Button onClick={() => handleInBasket({ id, img, name, text, weight, price })} variant="contained"> </Button>
                           </CardActions>
                        </Card>
                     </Grid>
                  ))}
               </Grid>
            </Container>
         </Box>
      </Box >
   )
}