import * as React from 'react';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import { BasketBtn } from '../BasketBtn/BasketBtn';
import { context } from '../../context/context';
import { Button, styled, Typography } from '@mui/material';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';

const Img = styled('img')`
max-width: 100%
`

export const Basket = () => {
   const [state, setState] = React.useState(false);
   const ctx = React.useContext(context)


   const toggleDrawer = (anchor, open) => (event) => {
      if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
         return;
      }

      setState({ ...state, [anchor]: open });
   };

   const handleAddSushi = (position) => {
      ctx.addSushi(position)
   }

   const handleRemoveSushi = (position) => {
      ctx.removeSushi(position)
   }


   const list = (anchor) => (
      <Box
         sx={{
            width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250,
            p: '10px',
            display: 'flex',
            flexDirection: 'column',
            gap: '10px'
         }}
         role="presentation"
         onKeyDown={toggleDrawer(anchor, false)}
      >
         <Typography variant='h4' textAlign='center' component='h4'></Typography>
         {ctx.sushiInBasket && ctx.sushiInBasket.map(({ id, img, name, price, text, weight, counter }) => (
            <Box key={id} sx={{ display: 'flex', flexDirection: 'column', textAlign: 'center', p: '5px', gap: '5px', border: '1px solid #ccc' }}>
               <Box maxWidth='200px' mx='auto' >
                  <Img src={img} alt={name} />
               </Box>
               <Box fontWeight='700'>{name}</Box>
               <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', gap: '20px', }}>
                  <RemoveCircleIcon sx={{ cursor: 'pointer' }} onClick={() => handleRemoveSushi({ id, img, name, price, text, weight, counter })} />
                  <Box>{counter}</Box>
                  <AddCircleIcon sx={{ cursor: 'pointer' }} onClick={() => handleAddSushi({ id, img, name, price, text, weight, counter })} />
               </Box>
               <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                  <Box fontWeight='700'>{weight}.</Box>
                  <Box fontWeight='700'>{price * counter} .</Box>
               </Box>
            </Box>
         ))}
         <Typography> : {ctx.sushiInBasket ? ctx.sushiInBasket.reduce((acc, num) => acc += num.counter, 0) : 0} .</Typography>
         <Typography fontWeight='700'> : {ctx.sushiInBasket ? ctx.sushiInBasket.reduce((acc, num) => acc += num.price * num.counter, 0) : 0} .</Typography>
         <Button variant="contained"> </Button>
      </Box>
   );

   return (
      <div>
         {['right'].map((anchor) => (
            <React.Fragment key={anchor}>
               <Box onClick={toggleDrawer(anchor, true)}>
                  < BasketBtn />
               </Box>
               <Drawer
                  anchor={anchor}
                  open={state[anchor]}
                  onClose={toggleDrawer(anchor, false)}
               >
                  {list(anchor)}
               </Drawer>
            </React.Fragment>
         ))}
      </div>
   );
}