import { useState } from "react";
import { context } from './context'
import DB from '../db.json'

export const Provider = ({ children }) => {
   const [sushiList, setSushiList] = useState(DB);
   const [sushiInBasket, setSushiInBasket] = useState(() => JSON.parse(window.localStorage.getItem('sushiInBasket') ?? null));

   const sortByPrice = () => {
      sushiList.sort((a, b) => a.price - b.price)
   }

   const sortByWeight = () => {
      sushiList.sort((a, b) => b.weight - a.weight)
   }

   const getSushi = (sushiList) => {
      setSushiList(sushiList)
   }

   const addSushi = (position) => {
      if (!sushiInBasket) {
         setSushiInBasket([])
      }
      setSushiInBasket((prevState) => {
         const index = prevState.findIndex((item) => item.id === position.id);
         if (index !== -1) {
            const updatedSushi = [...prevState];
            updatedSushi[index] = { ...updatedSushi[index], counter: updatedSushi[index].counter + 1 };
            return updatedSushi;
         } else {
            return [...prevState, { ...position, counter: 1 }];
         }
      });
   };

   const removeSushi = (position) => {
      setSushiInBasket((prevState) => {
         const index = prevState.findIndex((item) => item.id === position.id);
         if (index !== -1) {
            const updatedSushi = [...prevState];
            updatedSushi[index] = {
               ...updatedSushi[index], counter: updatedSushi[index].counter - 1,
            };
            return updatedSushi.filter((item) => item.counter > 0);
         } else {
            return prevState;
         }
      });
   };

   const providerValue = { sushiList, getSushi, sushiInBasket, addSushi, removeSushi, sortByPrice, sortByWeight }

   return (
      <context.Provider value={providerValue}>
         {children}
      </context.Provider>
   );
};